<template>
  <div class="list">
    <div class="item" v-for="(item, index) in newSongList" :key="item.id">
      <div class="icon-box">
        <span class="num">{{ index + 1 }}</span>
        <span class="icon el-icon-video-play" @click="playSong(index)"></span>
        <span class="icon1 el-icon-video-pause"></span>
      </div>
      <img :src="item.songImg" alt="" />
      <div class="singer">
        <h4 class="song-name">{{ item.songname }}</h4>
        <p class="singer-name">{{ item.singer }}</p>
      </div>
      <span class="blum ellipsis">《{{ item.blum }}》</span>
    
      <span class="time">{{ item.time | dateFormat('MM:SS') }}</span>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  
  props: {
    newSongList: {
      type: Array
    }
  },
  methods: {
    ...mapActions(['selectPlay']),
    playSong(index) {
      this.selectPlay({
        list: this.newSongList,
        index
      });
    }
  }
};
</script>

<style scoped lang="less">
.list {
  
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .item:hover {
    .icon-box .icon {
      display: block;
      color: red;
    }
    .icon-box .num {
      display: none;
    }
  }
  .item {
    width: 48%;
    background-color: #eee;
    display: flex;
    align-items: center;
    margin: 10px 0;
    padding: 15px;
    border-radius: 10px;
    .icon-box {
      width: 5%;
      text-align: center;
      font-size: 20px;
      margin-right: 10px;
      .num {
        font-size: 16px;
      }
      .icon {
        display: none;
      }
      .icon1 {
        display: none;
      }
    }
    img {
      width: 50px;
      height: 50px;
    }
    .singer {
      padding-left: 14px;
      margin-left: 10px;
      width: 35%;
      .song-name {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin-bottom: 10px;
      }
      .singer-name {
        font-size: 12px;
        color: #666666;
        font-weight: bold;
      }
    }
    .blum {
      width: 35%;
      font-size: 14px;
      font-weight: bold;
      margin: 0 20px;
    }
    .time {
      font-size: 14px;
      font-weight: bold;
    }
  }
}
</style>
