<template>
  <div id="recommend">
    <swiper :banner="banner" />
    <recommend-songs :recommendSong="recommendSong" />
    <recommend-private :privateData="privateData" />
    <recommend-music />
    <loading v-show="isLoading"></loading>

  </div>
</template>

<script>
import Swiper from '@/components/common/swiper/swiper'
import recommendSongs from './children/recommedSongs'
import recommendPrivate from './children/recommedprivate'
import recommendMusic from './children/recommedMusic'
import { banner, getRecommendSong ,getPrivateContent} from '@/network/recommend'
export default {
  components: {
    Swiper,
    recommendSongs,
    recommendPrivate,
    recommendMusic
  },

  data () {
    return {
      banner: [],
      recommendSong: [],
      limit: 27,
      privateData:[],
     isLoading:true
    }
  },
  mounted () {
    banner().then((res) => {
      this.banner = res.data.banners
    this.isLoading=false
    })
    getRecommendSong(this.limit).then(res => {
      this.recommendSong = res.data.result
    })
    getPrivateContent().then(res=>{
      this.privateData=res.data.result
    })
  }
}
</script>

