<template>
  <div class="limit">
    <h2>推荐新歌曲</h2>
    <div class="content">
      <song-list :newSongList="newSongList" />
    </div>
  </div>
</template>

<script>
import songList from '@/components/common/songList/songlist';
import { getNewSong } from '@/network/recommend';
import { getSongDetail, SongsInfo } from '@/network/detail';
export default {
  data() {
    return {
      newSongList: []
    };
  },
  components: {
    songList
  },
  created() {
    getNewSong().then((res) => {
      let list = [];
      res.data.result.forEach((item) => {
        list.push(item.id);
      });
      list = list.join(',');
      getSongDetail(list).then((res) => {
        res.data.songs.forEach((item) => {
          let list = new SongsInfo(item);
          this.newSongList.push(list);
        });
      });
    });
  }
};
</script>

<style scoped lang="less">
.content {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
  padding: 0 15px;
}
</style>
