import { axios } from './axios'
export function banner () {
  return axios({
    url: '/banner'
  })
}
// 推荐歌单
export function getRecommendSong (limit) {
  return axios({
    url: '/personalized',
    params: {
      limit
    }
  })
}
// 独家放送
export function getPrivateContent() {
  return axios({
    url: '/personalized/privatecontent'
  });
}
// 推荐新音乐
export function getNewSong(){
  return axios({
    url: '/personalized/newsong'
  });
}
